<template>
    <div>
        <van-nav-bar
                class="bar-wrapper"
                :title="this.$route.meta.title"
                left-arrow
                :z-index="999"
                @click-left="navBackClick"
        />
        <div class="cashier-wrapper container-wrapper">
            <div class="plr16 pt16">
                <div class="card ">
                    <div class="flex-content flex-content-align plr16 mb12"
                         :class="this.countDownCount ? 'flex-content-spaceBetween': 'flex-content-justify'">
                        <div class="cashier-account"><strong
                                v-if="this.amount">{{amount|$_filters_moneyFormat_noComma}}</strong></div>
                        <div class="countDown" v-show="this.countDownCount"><strong
                                class="danger">{{countDownCount}}s</strong></div>
                    </div>
                    <div class="danger align-c font-n-l"
                         v-if="!this.countDownCount && (this.breakOffStatus !== false)"></div>
                    <div class="cashier-code-container">
                        <div ref="code" id="code" class="flex-content flex-content-justify"></div>
                        <div class="mt10 font-s-l">POS</div>
                    </div>
                </div>
            </div>

            <div class="payType-box">
                <div class="cashier-tip"><i class="icon-redPacket"></i>APP</div>
                <ul class="payType-box-container">
                    <li class="payType-item">
                        <i class="icon-payKind icon-payKind_alipay"></i>
                        <div class="font-s-l"></div>
                    </li>
                    <li class="payType-item">
                        <i class="icon-payKind icon-payKind_wechat"></i>
                        <div class="font-s-l"></div>
                    </li>
                    <li class="payType-item">
                        <i class="icon-payKind icon-payKind_wm"></i>
                        <div class="font-s-l"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        Tag,
        Col,
        Icon,
        Cell,
        CellGroup,
        Swipe,
        Toast,
        SwipeItem,
        GoodsAction,
        GoodsActionBigBtn,
        GoodsActionMiniBtn,
        Dialog
    } from 'vant';
    import storeData from './store/index';
    import QRCode from 'qrcodejs2';
    import ajax from '@/api/cashier';

    export default {
        components: {
            [Tag.name]: Tag,
            [Col.name]: Col,
            [Icon.name]: Icon,
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup,
            [Swipe.name]: Swipe,
            [SwipeItem.name]: SwipeItem,
            [GoodsAction.name]: GoodsAction,
            [GoodsActionBigBtn.name]: GoodsActionBigBtn,
            [GoodsActionMiniBtn.name]: GoodsActionMiniBtn,
            QRCode: QRCode,
            Dialog: Dialog
        },
        data() {
            return Object.assign(storeData.call(this), {
                amount: undefined,
                buildingTime: 60,
                payOrderNo: '',
                countDownInterval: undefined,
                countDownCount: 0,
                queryOrderInterval: undefined,
                queryOrderCount: 0,
                orderOverStatus: false,
                breakOffStatus: false
            });
        },
        created() {
            if (this.$route && this.$route.query) {
                this.codeUrl = this.$route.query;
                this.payOrderNo = this.$route.query.payOrderNo || '';
                this.amount = this.$route.query.amount || '';
                if (Object.keys(this.codeUrl).length === 0) {
                    this.failCodeBuild();
                } else {
                    this.codeBuilding();
                    this.countDown();
                    this.queryOrder();
                }
            }
            this.$nextTick(function () {
                this.codeCanvas();
            });
        },
        methods: {
            codeBuilding() {
                Toast.loading({
                    mask: true,
                    message: '',
                    duration: 0,
                    className: 'buildingToast'
                });
            },
            codeCanvas() {
                let code = new QRCode('code', {
                    width: 235,  // 
                    height: 235, // 
                    text: this.codeUrl.code || ''
                });
                if (this.codeUrl.code) {
                    Toast.clear();
                }
            },
            countDown() {
                let self= this;
                self.countDownCount = 180;
                self.countDownInterval = window.setInterval(() => {
                    self.countDownCount--;
                    if (self.countDownCount > 0 || self.countDownCount === -2) {
                    } else {
                        self.countDownCount = 0;
                        if (self.countDownCount === 0) {
                            self.orderOverStatus = true;
                            if (self.codeUrl.code) {
                                Toast.clear();
                            }
                        }
                        self.orderOverTimeAction(self.orderOverStatus);
                        window.clearInterval(self.countDownInterval);
                    }
                }, 1000);
            },
            queryOrder() {
                let self = this;
                let _count = self.queryOrderCount;

                    self.queryOrderInterval = window.setInterval(() => {
                        _count++;

                        if (_count > 5*30) { //  55 
                            _count = 0;
                            window.clearInterval(self.queryOrderInterval);
                        }
                        queryOrderFetch(_count);
                    }, 2000); // 2s 

                function queryOrderFetch(_count) {
                    ajax.queryOrder({
                        payOrderNo: self.payOrderNo
                    }).then(response => {
                        if (!response.success === true) {
                            Toast(response.msg || '');
                            return;
                        } else {
                            if (response.data) {
                                if (response.data.payOrderStatus) {

                                    // 0", "",
                                    // "2", "",
                                    // "3", "",
                                    // "4", "",
                                    // "5", "",
                                    // "8", "",
                                    // "9", ""

                                    let payOrderStatus = response.data.payOrderStatus;
                                    if (payOrderStatus === '0' || payOrderStatus === '8') {
                                        // _count = 0;
                                        console.log(_count)
                                        // clearInterval(self.queryOrderInterval);
                                    }

                                    // 2 
                                    if (payOrderStatus === '2') {
                                        _count = 0;
                                        clearInterval(self.queryOrderInterval);
                                        window.clearInterval(self.countDownInterval);
                                        setTimeout(() => {
                                            self.$router.push({
                                                name: 'cashierSuccess'
                                            });
                                        }, 800);
                                    }
                                }
                            } else {
                                Toast(response.msg || '');
                                return;
                            }
                        }
                    }).catch(() => {
                        Toast('');
                        return;
                    });
                }
            },
            orderOverTimeAction(status) {
                if (status === true) {
                    this.orderOverStatus = true;

                    Dialog.confirm({
                        title: '',
                        message: '?',
                        showCancelButton: true,
                        confirmButtonText: '',
                        cancelButtonText: ''
                    }).then(() => {
                        Toast.clear();
                        this.breakOffStatus = true;
                        setTimeout(() => {
                            this.$router.push({
                                name: 'cashier',
                                query: ''
                            });
                        }, 800);
                        this.queryOrderCount = undefined;
                        window.clearInterval(this.countDownInterval);
                        window.clearInterval(this.queryOrderInterval);
                    }).catch(() => {
                        Toast.clear();
                        // this.countDown();
                    });
                }
            },
            failCodeBuild() {
                Dialog.confirm({
                    title: '',
                    message: '',
                    showCancelButton: false,
                }).then(() => {
                    window.clearInterval(this.countDownInterval);
                    window.clearInterval(this.queryOrderInterval);

                }).catch(() => {
                    window.clearInterval(this.countDownInterval);
                    window.clearInterval(this.queryOrderInterval);
                });
            },
            navBackClick() {
                if (this.countDownCount) {
                    Dialog.confirm({
                        title: '',
                        message: '',
                        showCancelButton: true,
                        confirmButtonText: '',
                        cancelButtonText: ''
                    }).then(() => {
                        Promise.all([this.cancelFetch()]).then((results) => {
                            if (results && results.length > 0) {
                                if (results[0]) {
                                    // 0", "",
                                    // "2", "",
                                    // "3", "",
                                    // "4", "",
                                    // "5", "",
                                    // "8", "",
                                    // "9", ""

                                    if (results[0].success) {
                                        this.orderOverStatus = false;
                                        this.countDownCount = undefined;

                                        window.clearInterval(this.countDownInterval);
                                        window.clearInterval(this.queryOrderInterval);

                                        setTimeout(() => {
                                            this.$router.push({
                                                name: 'cashier'
                                            });
                                        }, 800);
                                    }
                                }
                            }
                        });
                    }).catch(() => {
                    });

                } else {
                    this.countDownCount = undefined;
                    this.queryOrderCount = 0;

                    window.clearInterval(this.countDownInterval);
                    window.clearInterval(this.queryOrderInterval);
                    setTimeout(() => {
                        this.$router.push({
                            name: 'cashier'
                        });
                    }, 800);
                }
            },
            cancelFetch() {
                return new Promise((resolve, reject) => {
                    ajax.cancelOrder({
                        payOrderNo: this.payOrderNo
                    }).then(response => {
                        if (!response.success === true) {
                            if (response.errorNo == '103039') {
                                this.countDownCount = undefined;
                                this.queryOrderCount = 0;

                                window.clearInterval(this.countDownInterval);
                                window.clearInterval(this.queryOrderInterval);
                                setTimeout(() => {
                                    this.$router.push({
                                        name: 'cashier'
                                    });
                                }, 800);
                            } else {
                                Dialog.confirm({
                                    title: ',',
                                    message: '',
                                    showCancelButton: true,
                                    confirmButtonText: '',
                                    cancelButtonText: ''
                                }).then(() => {
                                    this.countDownCount = undefined;
                                    this.queryOrderCount = 0;

                                    window.clearInterval(this.countDownInterval);
                                    window.clearInterval(this.queryOrderInterval);
                                    setTimeout(() => {
                                        this.$router.push({
                                            name: 'cashier'
                                        });
                                    }, 800);
                                }).catch(() => {
                                });
                                return;
                            }
                        } else {
                            if (response.data) {
                              return resolve(response);
                            } else {
                                Toast(response.msg || '');
                                return reject({});
                            }
                        }
                    }).catch(() => {
                        Toast('');
                        return reject({});
                    });
                })

            }
        },
        beforeDestroy() {
            window.clearInterval(this.countDownInterval);
            window.clearInterval(this.queryOrderInterval);
            Toast.clear();
        }
    };
</script>

