                vertical-align: middle;
                margin-right: 6px;
                background-image: url("../../images/icon_redPacket@2x.png");
                background-repeat: no-repeat;
                background-position: top center;
                background-size: 100% auto;
            }
            .payType-box {
                padding-top: 13px;
                text-align: center;
                &-container {
                    margin-top: 14px;
                    display: inline-block;
                }
                .payType-item {
                    display: inline-block;
                    margin-left: 10vw;
                    margin-right: 10vw;
                    .font-s-l {
                        margin-top: 2px;
                    }
                }
            }
        }
        &-account {
            color: @text-color;
            font-size: @font-large;
            line-height: 1.5;
            text-align: center;
            strong {
                font-size: @font-huge;
                color: @text-color-dark;
                font-weight: @font-weight_800;
            }
        }
        &-tip {
            width: 100%;
            background-color: @yellow;
            color: @redLight;
            font-size: @font-smaller;
            line-height: 1.5;
            padding: 7px 14px 7px 12px;
        }
        &-code-container {
            padding-left: 45px;
            padding-right: 45px;
            text-align: center;
            margin-top: 17px;
            img {
                display: inline-block;
                width: 100%;
            }
        }
    }

    .countDown {
        color: @text-color;
        font-size: @font-small;
        strong {
            display: inline-block;
            text-align: right;
            width: 30px;
        }
    }

    @media screen and (max-width: 374px) {
        .cashier-tip[data-v-6b7a064c] {
